body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
  font-family: "Barlow-Thin";
  src: local("Barlow-Thin"), url(/static/media/Barlow-Thin.72e9db32.ttf);
}

.home-page {
  text-align: center;
  margin: auto;
  color: white;
  background-image: url(/static/media/mvp-banner.e1143cc0.png);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  width: 100vw;
  height: 100vh;
}

.home-page > h1 {
  font-family: "Barlow", sans-serif;
  padding-top: 3%;
  font-size: 45px;
  width: 30vw;
  margin: auto;
}

#order-pizza {
  padding-right: 3%;
  padding-left: 3%;
  padding-top: 1%;
  padding-bottom: 1%;
  font-size: 15px;

  margin: auto;
  text-decoration: none;
  font-weight: bold;
  color: black;
  border-radius: 22px;
  background-color: yellow;
  border: 0px;
}

.order-header {
  background-color: #ce2829;
  display: flex;
  justify-content: center;
  padding-top: 5%;
  padding-bottom: 3%;
  padding-right: 20%;
}

.order {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.order-header a {
  text-decoration: none;
  color: white;
  font-size: 1rem;
  font-weight: 600;
}

.order-info {
  display: flex;
  flex-direction: column;
  align-items: flex start;
  justify-content: center;
  width: 60vw;
  margin: auto;
}
.point {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 60vw;
  justify-content: space-between;
}
.order-select {
  display: flex;

  justify-content: flex-start;
  width: 60vw;
  margin: auto;
  padding-top: 5%;
}
.order-extra {
  width: 60vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-top: 5%;
  margin: auto;
}
#special-text,
.order-total {
  margin: auto;
  width: 50vw;
  padding-top: 4%;
}

.order-name {
  padding-bottom: 2%;
}

.order-frame {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 0px;
}

.order-note {
  padding-top: 8%;
}
.row {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 60vw;
  margin: auto;
}
#pizza-form label {
  color: #5f5f5f;
  font-size: 1rem;
}

.order-extra label {
  color: #292929;
  font-size: 1.5rem;
}

input {
  font-size: large;
}

#special-text {
  font-family: "Barlow-Thin", sans-serif;
  font-weight: bold;
  font-size: 1.5rem;
}

.order-counter {
  display: flex;
  border: 1px solid;
  border-radius: 10px;
  width: 10vw;
  justify-content: center;
  background-color: #fdc913;
  margin: auto;
}
.count {
  display: flex;
  align-items: center;

  justify-content: center;
  width: 3vw;
  height: 4vw;
  background-color: aliceblue;
}
.order-counter button {
  width: 3vw;
  background-color: #fdc913;
  border: 0px solid;
  margin: auto;
}

.card {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 50vw;
  height: 150px;
  align-items: flex-center;
}

.button {
  display: flex;
  flex-direction: column;

  align-items: center;
}
#order-button {
  width: 50vw;
  height: 5vw;
  border-radius: 10px;
  margin: auto;
  background-color: #fdc913;
}
.finally {
  border: 1px solid;
  border-radius: 10px;
}

.logo {
  height: 35px;
  padding-top: 5%;
}
.header {
  text-align: center;
  background-color: #ce2829;
}
@font-face {
  font-family: "Barlow";
  src: local("Barlow-Thin"), url(/static/media/Barlow-Thin.72e9db32.ttf);
}

